*{
    margin:0;
    padding:0;
}
ul ol{
 list-style:none;
}
.hsz-header{
    height:60px;
}
.hsz-con{
    height:600px;
    border-bottom: 1px solid #e6e6e6;
    background-color:#fff;
}
.hsz-con ul{
    height:50px;
    border-bottom: 1px solid #e6e6e6;
}
.hsz-con ul:hover{
    background: #efecec;
}
.hsz-con ul li{
    width:150px;
    height:50px;
    line-height:50px;
    float:left;
}
.hsz-con  span{
    float:right;
    display:block;
}