
.cd{
    height:100vh;
    float: left;
    overflow: auto;
    background: #001529;
}

.zj>p{
    display: block;
    width: 80%;
    height: 30px;
    margin: 10px auto;
    background-color: #3e84e9;
    line-height: 30px;
    font-size: 14px;
    text-align: center;
}
.cd ul{
    max-width: 200px;
    position: relative;
    padding-bottom: 100px;
}
.cd ul button{
    float: right;
    background-color: black;
    border: none;
    position: absolute;
    bottom: 0px;
    right: 10px;
    z-index: 999;
}
.zj button{
    float: right;
    background-color: black;
    border: none;
    position: absolute;
    bottom: 0px;
    right: 10px;
    z-index: 999;
}
.zj{
    max-width: 200px;
    position: relative;
    height: 600px;
    padding-bottom: 100px;
}

.cd::-webkit-scrollbar{
    width: 0;
}
.site-calendar-demo-card p{
    height: 45px;
    padding: 5px 10px;
    line-height: 45px;
    font-size: 14px;
}
.site-calendar-demo-card p span:nth-of-type(1){
    float: left;
}
.site-calendar-demo-card p span:nth-of-type(2){
    float: right;
    color: #3e84e9;
}
.xxxx{
    float: left;
}