*{
    margin: 0;
    padding: 0;
}
.clearfix::after{
    content: '';
    display:block;
    clear: both;
}
.bj{
    width: 70%;
    height: 100vh;
    float: left;
    background-image: url('http://crm.cimns.com/static/img/login.bc884f1.png');
    background-size: 100%;
}
.bj img{
    width: 180px;
    height: 48px;
    margin: 50px;
}
.dl{
    width: 30%;
    float: left;
    padding-top: 80px;
}
.dl>p{
    text-align: center;
    font-size: 26px;
    font-family: MicrosoftYaHei,Avenir,Helvetica,Arial,sans-serif;
    margin-bottom: 50px;
}
.dl input{
    display: block;
    width: 266px;
    padding: 0 12px;
    height: 40px;
    margin: 0 auto;
    margin-bottom: 20px;
    border: none;
    border-bottom: 1px #ccc solid;
    outline: none;
}
.dl button{
    background-color: #00aaee;
    width: 290px;
    height: 40px;
    font-size: 16px;
    line-height: 40px;
    border: none;
    display: block;
    margin: 0 auto;
    border-radius: 5px;
    color: #fff;
}