.gg{
    width: 918px;
    background-color: #fff;
    padding: 10px;
    margin: 20px;
    height: 84vh;
    position: relative;
    overflow: auto; 
}
.gg::-webkit-scrollbar{
    width: 0;
}
.gg>div:nth-of-type(1){
    margin-bottom: 20px;
}
.gg>div:nth-of-type(2)>div img{
    height: 40px;
    margin-bottom: 15px;
    border-radius: 50%;
    float: left;
    margin-right: 10px;
}
.gg>div:nth-of-type(2)>div p{
    display: inline-block;
    margin: 0;
    font-size: 14px;
}
.gg>div:nth-of-type(2)>div>div:nth-of-type(1) p:nth-of-type(2){
    color:#999;
    font-size: 12px;
}
.gg>div:nth-of-type(2)>div>div:nth-of-type(2){
    margin: 0 20px;
}
.gg>div:nth-of-type(2)>div>div:nth-of-type(2) span{
    display: block;
    font-size: 14px;
}
.gg>div:nth-of-type(2)>div>div:nth-of-type(2) p{
    font-size: 12px;
    padding: 15px;
    background-color: #f0f7ff;
    display: block;
    margin: 10px 0;
}
.cj{
    height: 100%;
    overflow: auto;
}
.cj::-webkit-scrollbar{
    width: 0;
}
.ant-modal-body{
    height: 80%;
}
    
