.clearfix::after{
    content: '';
    display:block;
    clear: both;
}
.for{
    width: 700px;
    height: 130px;
    padding: 5px 0 20px 0;
    margin: 0 auto;
}
.for>div:nth-of-type(1) img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
}
.for div:nth-of-type(1){
    height: 100%;
    float: left;
}
.pp{
    width: 665px;
}
.pp p:nth-of-type(1){
    margin-left: 10px;
    float: left;
    font-size: 15px;
    margin-bottom: 0;
}
.pp p:nth-of-type(2){
    margin-left: 10px;
    float: right;
    margin-bottom: 0;
}
.pp p:nth-of-type(1) span{
    margin-left: 10px;
    margin-bottom: 0;
    font-size: 13px;
}
.span{
    display: block;
    margin-left: 10px;
    font-size: 12px;
    color: #999;
    
}
.vv{
    width: 650px;
    padding: 0 5px;
    background-color: #f4f7fd;
    line-height: 37px;
    margin-top: 10px;
    float: left;
}
