.clearfix::after{
    content: '';
    display:block;
    clear: both;
}
.rz{
    padding: 10px;
    border: 1px #999 solid;
    margin-bottom: 10px;
}
.rz>div>div:nth-of-type(1){
    width: 35px;
    height: 100%;
    float: left;
    margin-right: 10px;
}
.rz>div>div:nth-of-type(1)>img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
}
.rz>div>div:nth-of-type(2){
    float: left;
}
.rz>div>div:nth-of-type(2) span:nth-of-type(1){
    font-size: 15px;
    margin-right: 10px;
}
.rz>div>div:nth-of-type(2) span:nth-of-type(2){
    font-size: 12px;
    color: #999;
}
.rz>div>div:nth-of-type(2) p{
    font-size: 12px;
    color: #999;
}
.rz>ul{
    padding-left: 45px;
    font-size: 13px;
    border-bottom: 1px #999 solid;
}
.rz>ul>li{
    color: #999;
}
.rz>ul>li>span{
    color: #000;
}
.rz1{
    margin-bottom: 10px;
}
.rz1>div>div:nth-of-type(1){
    width: 35px;
    height: 100%;
    float: left;
    margin-right: 10px;
}
.rz1>div>div:nth-of-type(1)>img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
}
.rz1>div>div:nth-of-type(2){
    float: left;
}
.rz1>div>div:nth-of-type(2) span:nth-of-type(1){
    font-size: 15px;
    margin-right: 10px;
}
.rz1>div>div:nth-of-type(2) span:nth-of-type(2){
    font-size: 12px;
    color: #999;
}
.rz1>div>div:nth-of-type(2) p{
    font-size: 14px;
    color: #333;
}
.hf{
    width: 100%;
    height: 40px;
    background-color: #f4f7fd;
}
.hf>button{
    width: 70px;
    height: 30px;
    margin: 5px 10px;
    background-color: #3e84e9;
    float: right;
    border: none;
    border-radius: 3px;
    color: #fff;
}
.hfnr{
    width: 100%;
    height: 110px;
    border: #999 solid 1px;
    display: none;
    margin-top: 10px;
}
.hfnr>div:nth-of-type(1){
    width: 100%;
    height: 60px;
    padding: 5px 10px;
}
.hfnr>div:nth-of-type(1) textarea{
    width: 100%;
    height: 100%;
    border: none;
    resize: none;
    outline: none;
}
.hfnr>div:nth-of-type(2){
    padding: 0 10px 10px 10px;
}
.hfnr>div:nth-of-type(2)>div{
    float: right;
    
}
.hfnr>div:nth-of-type(2)>div span{
    float: left;
    width: 70px;
    height: 30px;
    margin: 5px 10px;
    background-color: #3e84e9;
    float: left;
    border: none;
    border-radius: 3px;
    color: #fff;
    text-align: center;
    line-height: 30px;
}