.zxzx{
    height: 89vh;
    overflow: auto;
    overflow-y: scroll;
}
.zxzx::-webkit-scrollbar {
    width: 0;
}
.gzt{
    width: 768px;
    float: left;
    height: 95vh;
    margin: 15px;
    margin-bottom: 0;
    padding: 15px;
    overflow: auto;
    background-color: #fff;
}
.gzt::-webkit-scrollbar {
    width: 0;
}
.zyyy{
    float: left;
    margin: 15px;
}
.zyyy>div:nth-of-type(1){
    background-color: #fff;
    padding: 10px;
    margin-left: 0;
    width: 370px;
    height: 300px;
    border-radius: 5px;
}
.zyyy>div:nth-of-type(1) p{
    font-size: 14px;
    line-height: 44px;
    border-bottom: 1px #999 solid;
}
.zyyy>div:nth-of-type(1) img{
    display: block;
    width: 140px;
    height: 126px;
    margin: 0 auto;
    margin-top: 40px;
}
.zyyy>div:nth-of-type(1) span{
    display: block;
    font-size: 14px;
    color: #777;
    text-align: center;
    margin-top: 10px;
}
.site-calendar-demo-card {
    width: 100%;
    border: 1px solid #f0f0f0;
    border-radius: 2px;
  }
  .zyyy>div:nth-of-type(2){
    background-color: #fff;
    margin: 10px;
    margin-left: 0;
    width: 370px;
    border-radius: 5px;
}