*{
    margin:0;
    padding:0;
}
html,body{
    width:100%;
    height:100%;
}
.l-khbox{
    width:100%;
    height: 100%;
}
.cd{
    /* min-width:200px; */
    height:88vh;
    float:left;
}
.cd ul li{
    margin-right:100px;
}
.ly{
    width:1278px;
    height:88vh;
    padding:1%;
    background-color:#f5f6f9;
    float:left;
