
.clearfix::after{
    content: '';
    display:block;
    clear: both;
}
.box{
    width: 1500px;
    height: 89.9vh;
    position: relative;
} 
.nav>ul{
    margin: 0;
    border-bottom: 1px #ccc solid;
    background-color: #fff;
}
.nav>ul li{
    float: left;
    padding: 0 2%;
    color: #333;
    height: 10vh;
    font-size: 16px;
    list-style: none;
    line-height: 10vh;
}
.nav>ul>img{
    display: block;
    width: 130px;
    float: left;
    margin: 21px 20px;
}

.he-fr{
    float: right;
  }
  
  .he-vip-p{
    width: 70px;
    height: 30px;
    line-height: 30px;
    background: linear-gradient(90deg,#d9ac7c,#c28e5c);
    border-radius: 15px;
    color: #ffffff;
    font-size: 12px;
    text-align: center;
  }
  .he-vip{
    position: relative;
  }
  .ant-popover-inner-content h4{
    font-size: 16px;
    margin-bottom: 20px;
    font-weight: normal;
    text-align: center;
  }
  .ant-popover-inner-content p{
    font-size: 12px;
    
  }
  .ant-popover-inner-content-p{
    margin-bottom: 15px;
  }
  .ant-popover-inner-content .p3{
    background: linear-gradient(90deg,#f2dfb2,#e5c78a);
      border-radius: 4px;
      padding: 5px 17px;
      font-size: 12px;
      box-shadow: 0 2px 12px 0 rgb(0 0 0 / 12%);
      line-height: 17px;
      display: table;
      margin: 0 auto;
  }
  .he-vip{
    float: left;
    margin-right: 20px;
  }
  .he-fr-fr{
    float: left;
  }
  .he-fr-img{
    width: 30px;
    height: 30px;
  }
  .he-fr-img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .he-fr-img{
    float: left;
    margin-right: 10px;
  }
  .he-fr-fr-fr{
    float: left;
    margin-top: 5px;
  }
  .he-fr-fr-fr .iconfont{
    color: #aaa;
    border: none;
    background-color: transparent;
  }
  .zh{
    display:none;

  }



.p2{
    width: 100%;
    height: 30px;
  }
  .p2 button{
    width: 100%;
    height: 100%;
    border-radius: 4px !important; 
    border-color: #009df0 !important;
    background-color: #009df0 !important;
    color: #ffffff;
    font-size: 12px;
    text-align: center;
    line-height: 30px;
  }