.clearfix::after{
    content: '';
    display:block;
    clear: both;
}
.rili{
    width: 1260px;
    height: 88vh;
    float: left;
}
.hz{
    height: 88vh;
    float: left;
    overflow: auto;
    margin: 0 10px;
}
.hz::-webkit-scrollbar {
    width: 0;
}
.hz>div:nth-of-type(1){
    padding: 20px;
    background-color: #fff;
    font-size: 14px;
}
.hz>div:nth-of-type(1)>span{
    padding:  5px 10px;
    border: #666 solid 1px;
    border-radius: 5px;
    float: left;
}
.hz>div:nth-of-type(1)>p{
    padding:  5px 10px;
    float: left;
    margin-left: 50px;
}
.hz>div:nth-of-type(1)>p:nth-of-type(1)>span:nth-of-type(1){
    padding:  5px 10px;
    border: #666 solid 1px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.hz>div:nth-of-type(1)>p:nth-of-type(1)>span:nth-of-type(2){
    padding:  5px 10px;
    border: #666 solid 1px;
}
.hz>div:nth-of-type(1)>p:nth-of-type(1)>span:nth-of-type(3){
    padding:  5px 10px;
    border: #666 solid 1px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: blue;
    color: #fff;
}
.hz span{
    color:#000;
}
.hz>div:nth-of-type(1)>p:nth-of-type(2){
    margin-left: 25%;
    font-size: 25px;
}
.hz>div:nth-of-type(1)>p:nth-of-type(2)>span{
    margin: 0 20px;
}
.hz>div>span:nth-of-type(2){
    float: right;
    border: none;
    background-color: blue;
}
.hz>div>span:nth-of-type(2) a{
    color: #fff;
}
.ant-modal-content{
    width: 700px;
    height: 90vh;
    background-color: #fff;
    margin: 5vh auto;
}
.ant-modal{
    width: 100vw;
    height: 100vh;
}
.ant-modal-wrap{
    background-color: #f5f6f9;
}
.zt{
