@charset "utf-8";
*{margin: 0; padding: 0;}
ul,ol{list-style: none; }
img{vertical-align: top; }
a{text-decoration: none; }
em{font-style: normal;}
input{border: none; outline: none;background-color: transparent; }
button{border: none; outline: none; background-color: none;}
.clearfix::after,
.clearfix::before{content: '';display: table;}

.top1{
    margin:8px 0 20px 0;
}
  .top1>h3{
    margin:0;
    padding:0;
    font-weight: normal;
    text-align: left;
    color:#000;
    font-size: 20px;
  }
  .wdrw{
    width: 100%;
    height: 126px;
    padding:8px;
  }
  .wdrw h6{
    margin:0;
    padding:0;
    font-weight: 300;
    text-align: left;
    font-size: 15px;
    color:#999;
    letter-spacing:4px;
  }
  .wdrw h6 b{
    font-weight: normal;
    color: #333;
    letter-spacing:1px;
  }
  .wdrw>div{
    width:100%;
    height:6px;
    margin-top:7px;
    background-color: #e9e9e9;
    border-radius: 2px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
  }
  .wdrw>p{
    position: absolute;
    bottom:0px;
    color:#999;
  }
  .cont-rgt{
    padding-left: 20px;padding-top: 20px;
  }
  .con-let ,.ren,.ri{
    border-radius: 8px;
    border: solid 1px #ccc;
    padding: 20px;background-color: #fff;
  }
  .con-let{
    float: left;width: calc(70% - 30px);
    margin-right: 30px;
  }
  .con-rft { 
    float: left;
    width: 30%;
    height: 700px;
    margin: 0;
  }
  .proinput{
    width:45%;
    margin-bottom:25px;
  }
  .proinput>input{
    width: 100%;
    margin:0;
    border:1px solid #ccc;
    border-radius: 4px;
    height:35px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    text-indent: 5px;
  }
  .prolist{
    display: flex;
  }
  .prolist>li{
    list-style: none;
    width:36px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    margin: 0 5px;
    border-radius: 50%;
  }