.wd{
    padding: 10px;
}
.input input{
    width: 290px;
    height: 36px;
    padding-left: 8px;
    padding-right: 30px;
    outline: none;
}
.input+ul{
    margin-top: 20px;
}
.input+ul>li{
    float: left;
    margin-right: 15px;
    margin-bottom: 10px;
}
.input+ul>li>span{
    color: #999;
    margin-right: 10px;
}
.ant-select{
    border-radius: 3px;
    margin-left: 10px;
}
.input+ul+div+div{
    text-align: center;
    padding-top: 20px;
}
.xxr{
    margin: 0 20px;
    border-left: 2px rgb(237, 99, 99) solid;
    padding: 10px;
    border-bottom: 1px #e6e6e6 solid;
    margin-bottom: 1px;
}
.xxr>span{
    display: block;
    width: 14px;
    height: 14px;
    border: 1px #e6e6e6 solid;
    float: left;
    margin-top: 8px;
}
.xxr>p{
    float: left;
    margin-left: 10px;
}
.xxr div{
    float: right;
}
.xxr div img{
    width: 24px;
    height: 24px;
    margin-left: 15px;
    border-radius: 50%;
}