.clearfix::after{
    content: '';
    display:block;
    clear: both;
}

.kh_1 input{  
	background:none;  
	outline:none;  
}

.kh_1{
    width: 100%;
    height: 100%;
    float: left;
}
.kh_1-header{
    width: 1200px;
    height:47px;
    margin-bottom:5px;
}
.kh_1-use{
    float:left;
}
.kh_1-use-img{
    margin-right:15px;
    float:left; 
}
.kh_1-use-name{
    font-size: 16px;
    color: #333;
    margin-right:15px;
    float:left;
    line-height:40px;
}
.kh_1-use-btn{
    float:left;
}
.kh_1-use-btn span{
    font-size: 12px;
    margin-right: 15px;
    color: #3e84e9;
    line-height:40px;
}
.kh_1-use-year{
    float:left;
    line-height:40px;
}
.kh_1-use-sjcc{
    line-height:40px;
    float:right;
}
.kh_1-content{
    width: 100%;
    height:78vh;
}
.kh_1-content-1{
    width:580px;
    height:310px;
    float:left;
    padding:10px;
    margin: 14px;
    border: 1px solid #e6e6e6;
    background-color: #fff;
}
.kh_1-content-title{
    height: 14px;
    margin:0 0 20px;
    color:#333;
}
.kh_1-content-title-1 h4{
    padding-left: 25px;
    font-size: 13px;
    background:url(data:image/png;base64,)no-repeat;
    
}
.kh_1-content-title-2 h4{
    padding-left: 25px;
    font-size: 13px;
    float:left;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-title-2 div{
    float:right;
}
.kh_1-content-title-3{
    height:30px;
}
.kh_1-content-title-3 h4{
    padding-left: 25px;
    font-size: 13px;
    float:left;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-title-3 div{
    float:right;
}
.kh_1-content-title-3-con{
    width:480px;
    height:260px;
}
.kh_1-content-title-3-con-left{
    width:200px;
    height:240px;
    float:left;
    margin-left:20px;
    display: table;
}
.kh_1-content-title-3-con-left-1{
    display: table-cell;
    vertical-align: middle;
}
.kh_1-content-title-3-con-left-1 h4{
    color:#6ca2ff;
    font-size:13px;
}
.kh_1-content-title-3-con-left-1 h5{
    margin-top: 10px;
    color: #ff7474;
    font-size:13px;
}
.kh_1-content-title-3-con-right{
    width:100px;
    height:240px;
    display: table;
    float:right;
}
.kh_1-content-title-3-con-right ul{
    display: table-cell;
    vertical-align: middle;
}
.kh_1-content-title-3-con-right ul li{
    font-size:14px;
    line-height:32px;
    color:#6ca2ff;
}
.kh_1-content-title-3-con-right ul li:hover{
    font-size:18px;
}
.kh_1-content-title-3-con-right ul li:nth-of-type(1){
    color:#6ca2ff;
}
.kh_1-content-title-3-con-right ul li:nth-of-type(2){
    color:#6ac9d7;
}
.kh_1-content-title-3-con-right ul li:nth-of-type(3){
    color:#72dca2;
}
.kh_1-content-title-3-con-right ul li:nth-of-type(4){
    color:#dbb375;
}
.kh_1-content-title-4 h4{
    padding-left: 25px;
    font-size: 13px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-title-4-con{
    height:50px;
}
.kh_1-content-title-4-con-left{
    width:300px;
    float:left;
}
.kh_1-content-title-4-con-left-1{
    width:103px;
    height:60px;
    float:left;
}
.kh_1-content-title-4-con-left-1 h5{
    color:#666;
    padding:5px 25px 5px;
}
.kh_1-content-title-4-con-left-1 span{
    color:#333;
    display:block;
    font-size:14px;
}
.kh_1-content-title-4-con-right{
    width: 100px;
    float:right;
}
.kh_1-content-i{
    width:270px;
    height:39px;
    color:#333;
    margin:8px 3px;
    background-color: blue;
    padding: 8px;
    background-color: #f2f2f5;
    border-radius: 3px;
    float:left;
}
.kh_1-content-i h3{
    color: #333;
    font-size: 12px;
    vertical-align:middle;
    background-position:center;
    margin-right: 10px;
    padding-left:40px;
    position: relative;
}
.kh_1-content-i h3 span{
    float:right;
    display:block; 
}
.kh_1-content-i:nth-child(2) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(3) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(4) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(5) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(6) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(7) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
.kh_1-content-i:nth-child(8) h3{
    background-image:23px 23px;
    background:url(data:image/png;base64,)no-repeat;
}
#main{
    margin: 0 auto;
    padding: 0;
}
.kh_1-content-hj{
    height: 50px;
    color:#333;
    display:flex;
}
.target-item{
    /* width:100px;*/
    /* height:84px;  */
    flex:1;
}
.target-item-name{
    color:#666;
    text-align:center;
    font-size:12px;
}
.target-value{
    color:#333;
    text-align:center;
    font-size:14px;
    margin-top:5px;
}
.kh_1-sjcc{
    width:658px;
    height: 480px;
    overflow-y: auto;
    padding: 0 30px;
}
.kh_1-sjcc-b{
    height:162px;
    width:598px;
}

.kh_1-sjcc-1{
    width:299px;
    height:81px;
    float:left;
    /* padding:0 20px 0 0; */
}
.kh_1-sjcc-1 input{
    border: 1px solid #ddd;
}
.kh_1-sjcc-btn button{
    display:block;
